* {
    background: #111111;
}

:root {
	
	--primary: #111111;
	--light-shadow: #ffffff; 
    --dark-shadow: #a3b1c6;
    --accent-shadow: hsl(351, 80%, 57%);
	--shadow-size: 0.30rem;
	--shadow-blur: calc(2 * var(--shadow-size));
	--text-base: rgba(0, 0, 0, 0.8);
}
@media (prefers-color-scheme: dark) {
	:root {
		--primary: hsl(0, 0%, 7%);
		--light-shadow: hsl(0, 0%, 9%); 
		--dark-shadow: hsl(0, 0%, 3%); 
        --text-base: rgba(255, 255, 255, 0.7);
        --accent: hsl(351, 80%, 57%);
	}
}
ul li {
    color: grey;
}

.main-text {
    color: #111111;
    font-size: 10em;
    text-shadow:
		var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
		calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);
   
}

#mainbox {
    animation: loop 8s infinite;
    -webkit-animation: loop 8s infinite;
}
@keyframes loop
{
  0%   {transform: rotate(0deg);}
100%   {transform: rotate(360deg);}
}

.button:active {
    box-shadow:
    var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
inset	calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);
}
.sb-btn:focus {
    transition: 0.3s;
    box-shadow:
		var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
	inset	calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);

}
.neumorphism:hover,
.button:hover {
	cursor: pointer;
	box-shadow: 
		calc(0.3 * var(--shadow-size)) calc(0.3 * var(--shadow-size)) calc(0.5 * var(--shadow-blur)) var(--dark-shadow),
		calc(-0.3 * var(--shadow-size)) calc(-0.3 * var(--shadow-size)) calc(0.5 * var(--shadow-blur)) var(--light-shadow);
}
.button {
    color: white ;
    background-color: var(--accent) ;
    transition: all 500ms;
    border: 0px;
    height: 44px;
    width: 65px;
    border-radius: 5px;
    outline: none;
    box-shadow:
    calc(0 * var(--shadow-size)) calc(0 * var(--shadow-size)) var(--shadow-blur) var(--accent-shadow),
		calc(0 * var(--shadow-size)) calc(0 * var(--shadow-size)) var(--shadow-blur) var(--accent-shadow);
}
.button p{
    background-color: var(--accent) ;
    margin: 0px;
}
.sb-btn {
    color: grey;
	transition:500ms;
    border: 0px;
    padding: 10px;
    border-radius: 5px;
    outline: none;
    background: #111111;
    box-shadow:
		var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
		calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);
}
.neumorphism {
    transition: all 500ms;
    cursor: default;
	background: transparent;
    border: none;
    outline: none;
    padding: 10px;
    margin: 40px 0px;
	
	box-shadow:
		var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
		calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);
	
}
.pokeball{
    height: 200px;
    width: 200px;  
    border-radius: 100%;
    padding: 10px;
    background: linear-gradient(0deg, #ffffff 51%, var(--accent) 52%);
    /* background: #111111; */
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow:
		var(--shadow-size) var(--shadow-size) var(--shadow-blur) var(--dark-shadow),
		calc(-1 * var(--shadow-size)) calc(-1 * var(--shadow-size)) var(--shadow-blur) var(--light-shadow);
  
}
.pokecenter {
    width: 28%;
    height: 50px;
    border-radius: 100%;
    padding: 10px;
    background: #111111;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
    
}
.minicenter{
    width: 30px;
    border-radius: 100%;
    height: 30px;
    background: #ffffff;
   
}
.pokecenterleft{
    height: 25px;
    width: 100%;
    position: absolute;
}

.pokeball-box {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 700px) {
    .main-text { 
        font-size: 5em;
    }

 }

